import React from 'react';

export const ComponentTab = (props: {
  title: string;
  Content: () => JSX.Element;
  ContextProvider?: (props: any) => JSX.Element;
}) => {
  const { title, Content, ContextProvider, ...childProps } = props;

  return ContextProvider ? (
    <ContextProvider {...childProps}>
      <Content />
    </ContextProvider>
  ) : (
    <Content />
  );
};
